@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  color-scheme: dark;
  --surface: rgba(15, 23, 42, 0.85);
  --ring: rgba(20, 184, 166, 0.3);
}

body {
  @apply bg-slate-950 text-slate-100 antialiased;
  background-image: radial-gradient(circle at top left, rgba(20, 184, 166, 0.25), transparent 55%),
    radial-gradient(circle at bottom right, rgba(245, 158, 11, 0.2), transparent 50%);
  background-attachment: fixed;
}

a {
  @apply text-accent-400 transition-colors duration-200;
}

a:hover {
  @apply text-accent-300;
}
